.Nav {
  display: -ms-flexbox;
  display: flex;
  background-color: var(--deep-purple);
}

.Nav__links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  width: 40%;
